    margin-top: 20px;
    table-layout: fixed;
    tbody {
      tr {
        th:nth-child(1) {
          overflow: hidden;
          white-space: nowrap;
          text-overflow: ellipsis;
        }
      }
    }
  }
  .com-hint {
    
  }
}



