.virus-box {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
    & > div {
        display: flex;
        text-align: center;
        align-items: center;
        border: 2px solid #0ff;
        border-radius: 6px;
        width: 45%;
        background: rgba(17, 76, 233, 0.2);
        cursor: pointer;
        &:hover {
            background: rgba(17, 76, 233, 0.3);
        }
        .name {
            background: #3d3cc7;
            font-size: 30px;
            line-height: 80px;
            color: #fff;
            width: 188px;
            height: 80px;
        }
        .message {
            box-sizing: border-box;
            padding: 0 30px;
            flex-grow: 1;
            text-align: right;
            font-size: 48px;
            color: #36ebff;
            font-family: digifacewide;
            sub {
                margin-left: 10px;
                color: #fff;
                font-size: 27px;
            }
        }
        &:nth-child(2) .message{
            color: #ac8cff;
        }
    }
}


