.internet-bug-box {
    table {
        .header {
            background: url(./images/tab-header.png);
            background-size: 100% 100%;
            th {
                padding: 15px 0;
            }
        }
        tbody {
            background: url(./images/tab-body.png)repeat-x;
            .row {
                background: none;
                th:nth-child(1) {
                    overflow: hidden;
                    white-space: nowrap;
                    text-overflow: ellipsis;
                }
            }
        }
    }
}

#interbugTable {
    position: relative;
    min-height: 430px;
}


