.com-table{
  table {
    position: relative;
    z-index: 1;
    width: 100%;
    margin: 68px auto 0 auto;

    .header {
      font-size: 32px;
      color: #9dc9ff;
      th {
        padding-bottom: 30px;
      }
    }

    .row {
      font-size: 26px;
      color: #FFF;
      background: url(./images/table-bg.png) no-repeat center / 100% 100%;
      &:hover {
        background: url(./images/table-item-emphasis.png) no-repeat center / 100% 100%;
      }
      th {
        padding: 24px 0;
        &.attack, &.intercept, &.attachment {
          cursor: pointer;
        }
        &.attachment {
          a {
            color: #ffd200;
            text-decoration: none;
          }
          a:hover {
            text-decoration: underline;
          }
        }
      }
    }
  }
}


