.time-main {
    position: absolute;
    left: -10px;
    bottom: 70px;
    width: 100%;
    text-align: center;
    & > span {
        margin: 0 5px;
        box-sizing: border-box;
        display: inline-block;
        width: 222px;
        height: 70px;
        background: url(./images/timg-bg.png);
        background-position: 0px -85px;
        font-size: 30px;
        color: #fff;
        line-height: 70px;
        cursor: pointer;
        
        &.active,
        &:hover {
            color: #ffd200;
            background-position: -240px -85px;
        }
        &:nth-child(1) {
            padding-left: 10px;
            width: 188px;
            background-position: 0 -12px;
            &.active,
            &:hover {
                background-position: -240px -12px;
            }
        }
        &:nth-last-child(1) {
            padding-right: 10px;
            width: 188px;
            background-position: 0 -172px;
            &.active,
            &:hover {
                background-position: -240px -172px;
            }
        }
    }
    
}


