.frame-main {
    margin-top: 110px;

    &::after {
        content:".";
        display:block;
        height:0;
        clear:both;
        visibility:hidden;
    }
    .intranet-main,
    .internet-main {
        box-sizing: border-box;
        position: relative;
        padding: 40px 0;
        left: 110px;
        float: left;
        width: 1520px;
        height: 1420px;
    
    }
    .intranet-main {
        &::before {
            position: absolute;
            top: -40px;
            left: 290px;
            content: '内网终端';
            color: #fff;
            font-size: 40px;
        }
        .monitor {
            height: 730px;
        }
        .virus {
            height: 560px;
            display: flex;
        }
    }
    .internet-main {
        margin-left: 90px;
        &::before {
            position: absolute;
            top: -40px;
            left: 290px;
            content: '互联网终端';
            color: #fff;
            font-size: 40px;
        }
        .internet-box {
            height: 1310px;
        }
    }

    .frame-sty {
        position: relative;
        box-sizing: border-box;
        margin: 20px 0;
        width: 100%;
        padding: 35px 50px;
        /* height: 300px; */
        border: 2px solid #053ea3;
        background: url(./images/frame-bg.png);
        &::before,
        &::after {
            position: absolute;
            content: url(./images/frame-border.png);
            left: 0;
        }
        &::before {
            top: -10px;
        }
        &::after {
            bottom: -10px;
            transform: rotate(180deg);
        }
    }
}

/* 临时为二级弹窗返回时写的 */
#modalReturn {
    display: none;
    position: absolute;
    bottom: 50%;
    right: 50%;
    transform: translate(709px, 496px);
    cursor: pointer;
    width: 220px;
    height: 78px;
    line-height: 78px;
    background: url(./images/btn-bg.png) no-repeat center / 100% 100%;
    font-size: 30px;
    color: #36ebff;
    text-align: center;
    text-shadow: 0 0 15px #36ebff;
    z-index: 9999;
}


