.statistics-box {
    display: flex;

    .base-box {
        .name {
            margin: 30px 0 10px;
            color: #fff;
            font-size: 40px;
        }
        .value {
            color: #ffd200;
            font-size: 78px;
            font-family: digifacewide;
        }
        cursor: pointer;
    }

    .catalog-box {
        flex-grow: 1;
        display: flex;
        & > div {
            flex-grow: 1;
            text-align: center;
            .icon-reveal {
                position: relative;
                img {
                    margin-top: 24px;
                    opacity: 1;
                    animation: anmOpacity 1s linear infinite;
                }
                span {
                    position: absolute;
                    left: 50%;
                    transform: translate(-30px,0px);
                    display: inline-block;
                    width: 60px;
                    height: 60px;
                    border: 1px solid #fff;
                    background: rgba(7, 102, 228, 0.8);
                    border-radius: 100%;
                    
                    .icon {
                        display: inline-block;
                        transform: translate(0, 14px);
                        background: url(./images/icon.png);
                        width: 32px;
                        height: 32px;
                    }
                    
                }
            }
            .value {
                margin-top: 10px;
                font-size: 40px;
                color: #fffffe;
                text-shadow: 0 0 20px #074aa8;
            }
            .name {
                font-size: 24px;
                color: #fffffe;
            }
            &:nth-child(2) .icon-reveal span .icon{
                background-position: 0 -30px;
                height: 28px;
            }
            &:nth-child(3) .icon-reveal span .icon{
                background-position: 0 -62px;
            }
            &:nth-child(4) .icon-reveal span .icon{
                background-position: 0 -98px;
            }
        }
    }
}

@keyframes anmOpacity {
    0% {
        opacity: 1;
    }
    50% {
        opacity: 0.5;
    }
    100% {
        opacity: 1;
    }
}


