.violation-box {
    margin-top: 10px;
}
.violation-list {
    display: flex;
    margin-top: 20px;
    & li:nth-child(1),
    & li:nth-child(3) {
        .icon-box {
            img {
                animation: rotate1 4s linear infinite;
            }
        }
    }
    & li:nth-child(2),
    & li:nth-child(4)  {
        .icon-box {
            img {
                animation: rotate2 4s linear infinite;
            }
        }
    }
    li {
        display: flex;
        align-items: center;
        cursor: pointer;
        .icon-box {
            position: relative;
            img {
                width: 190px;
                
            }
            .icon {
                position: absolute;
                top: 50%;
                left: 50%;
                transform: translate(-48px, -36px);
                display: block;
                width: 106px;
                height: 60px;
                background: url(./images/icon.png);
            }
        }
        &:nth-child(2) .icon-box .icon {
            background-position: 0 -63px;
        }
        &:nth-child(3) .icon-box .icon,
        &:nth-child(4) .icon-box .icon {
            background-position: 0 -136px;
        }
        .name {
            margin-bottom: 10px;
            font-size: 24px;
            color: #fff;
        }
        .value {
            font-size: 50px;
            color: #ffd200;
        }
        sub {
            font-size: 24px;
            color: #fff;
            margin-left: 20px;
        }
    }
}

@keyframes rotate1 {
    from {
        transform: rotate(180deg)
    }
    to {
        transform: rotate(540deg)
    }
}
@keyframes rotate2 {
    from {
        transform: rotate(0deg)
    }
    to {
        transform: rotate(359deg)
    }
}


