.inter-statistics-box {
    margin-top: -20px;
    display: flex;
    align-items: center;
    & > div {
        position: relative;
        box-sizing: border-box;
        display: flex;
        flex-direction: column;
        justify-content: center;
        text-align: right;
        padding: 0 30px 0 120px;
        margin-right: 40px;
        width: 332px;
        height: 100px;
        border: 2px solid rgba(17, 76, 233, 0.5);
        background: rgba(17, 76, 233, 0.2);
        border-radius: 10px;
        border-bottom: 5px solid #114ce9;
        cursor: pointer;
        &:hover {
            background: rgba(17, 76, 233, 0.3);
        }
        &:nth-child(1)::before {
            position: absolute;
            content: url(./images/user.png);
            top: 10px;
            left: 34px;
        }
        &:nth-child(2) {
            border-bottom: 5px solid #b59f28;
        }
        &:nth-child(2)::before,
        &:nth-child(3)::before {
            position: absolute;
            content: url(./images/usb.png);
            top: 10px;
            left: 34px;
        }
        .name {
            font-size: 26px ;
            color: #fff;
            margin-bottom: 10px;
        }
        .value {
            font-size: 40px;
            background: linear-gradient(to top, #7aa6e3, #d3dbed);
            -webkit-background-clip: text;
            color: transparent;
            font-family: digifacewide;
        }
    }
}


