.internet-virus-box {
    .internet-virus-but {
        margin: 20px 0 10px; 
        .but-base {
            box-sizing: border-box;
            display: inline-block;
            padding: 0 30px;
            width: 600px;
            height: 78px;
            line-height: 78px;
            background: url(./images/bg.png) no-repeat;
            cursor: pointer;
            .name {
                margin-right: 20px;
                font-size: 33px;
                color: #fff;
            }
            .value {
                display: inline-block;
                min-width: 230px;
                text-align: right;
                font-size: 44px;
                color: #ffd200;
                font-family: digifacewide;
                sub {
                    margin-left: 20px;
                    font-size: 24px;
                    color: #fff;
                }
            }
        }
        & .but-base:nth-child(2) {
            .value {
                color: #36ebff;
            }
        }
        button {
            border: 0;
            width: 140px;
            height: 59px;
            color: #35ecfa;
            font-size: 28px;
            background: url(./images/but-bg.png);
            cursor: pointer;
            &:hover {
                background-position: 0 -62px;
            }
        }
    }
    /* 表格 */
    table {
        margin-top: 0; 
        table-layout: fixed;
        tbody {
            tr {
              th:nth-child(1) {
                overflow: hidden;
                white-space: nowrap;
                text-overflow: ellipsis;
              }
            }
        }
    }
}
#interVirusTable {
    position: relative;
    min-height: 430px;

    .header th{
        padding: 10px;
    }
}


